import { Grid } from '@taxfix/ui';
import React, { useState } from 'react';
import './controls.css';
import { TaxfixSlider } from '../slider';
import { throttle } from 'lodash';
import { Item } from '../../types';

type Props = {
  selectedItem?: Item;
  onChangeMonth: (month: number) => void;
  onChangeAmount: (amount: number) => void;
  month: number;
  amount: number;
};

const months = [
  'January',
  'February',
  'March',
  'April',
  'May',
  'June',
  'July',
  'August',
  'September',
  'October',
  'November',
  'December',
];

const LaptopControls = ({
  onChangeAmount,
  onChangeMonth,
  month,
  amount,
}: Props) => {
  const throttledSetAmount = throttle((val) => onChangeAmount(val), 30, {
    leading: true,
    trailing: true,
  });

  return (
    <>
      <Grid container justify="space-between" style={{ marginTop: '100px' }}>
        <Grid item xs={10}>
          <h2>When did you or will you buy a laptop for work in 2021?</h2>
        </Grid>
        <Grid item xs={2} container justify="flex-end">
          <div className="slider-value">{months[month]}</div>
        </Grid>
      </Grid>

      <TaxfixSlider
        min={0}
        max={11}
        marks
        value={month}
        onChange={(_event, val) => onChangeMonth(val as number)}
      />

      <Grid container justify="space-between" style={{ marginTop: '100px' }}>
        <Grid item xs={10}>
          <h2>What is the cost of the laptop?</h2>
        </Grid>
        <Grid item xs={2} container justify="flex-end">
          <div className="slider-value">
            {amount.toLocaleString('de', {
              currency: 'EUR',
              style: 'currency',
              maximumFractionDigits: 0,
            })}
          </div>
        </Grid>
      </Grid>

      <TaxfixSlider
        min={0}
        max={4000}
        step={1}
        value={amount}
        onChange={(_event, val) => throttledSetAmount(val as number)}
      />
    </>
  );
};

