class Depreciation {
  constructor(value, residual) {
    this.value = value;
    this.residual = residual;
  }
}

class DateIterator {
  constructor(startDate, years) {
    this.currentDate = new Date(startDate.getTime());
    let tmp_date = new Date(startDate.getTime());
    this.end_date = new Date(
      tmp_date.setMonth(tmp_date.getMonth() + years * 12)
    );
  }

  hasNext() {
    return this.currentDate < this.end_date;
  }

  next() {
    if (this.hasNext()) {
      this.currentDate = new Date(
        this.currentDate.setMonth(this.currentDate.getMonth() + 1)
      );
      return true;
    } else {
      return false;
    }
  }

  get date() {
    return this.currentDate;
  }

  get year() {
    return this.date.getFullYear();
  }
}

function getOrElse(map, key, default_value) {
  if (key in map) {
    return map[key];
  } else {
    return default_value;
  }
}

function truncateFinalResult(depreciationMap) {
  let newMap = {};
  for (let [year, depreciation] of Object.entries(depreciationMap)) {
    const truncatedValue = Number(depreciation.value).toFixed(2);
    let truncatedResidual = Number(depreciation.residual).toFixed(2);
    if (Math.abs(truncatedResidual) < Number(0.0001)) {
      truncatedResidual = Number(Math.abs(truncatedResidual)).toFixed(2);
    }
    newMap[year] = new Depreciation(truncatedValue, truncatedResidual);
  }

  return newMap;
}

function computeItemDepreciationPlan(purchaseDate, value, years) {
  let depreciationMap = {};
  if (value <= 952) {
    depreciationMap[purchaseDate.getFullYear()] = new Depreciation(value, 0);
  } else {
    let tempResidual = value;
    const monthlyDepreciation = Number(value / (12 * years)).toFixed(4);
    let i = new DateIterator(purchaseDate, years);

    while (i.hasNext()) {
      const tempDepreciation = getOrElse(
        depreciationMap,
        i.year,
        new Depreciation(0, tempResidual)
      );
      tempResidual = tempDepreciation.residual - monthlyDepreciation;
      const tempValue =
        Number(tempDepreciation.value) + Number(monthlyDepreciation);
      depreciationMap[i.year] = new Depreciation(tempValue, tempResidual);
      i.next();
    }
  }
  return truncateFinalResult(depreciationMap);
}

export { Depreciation, computeItemDepreciationPlan };
