import { ThemeProvider, Container, Grid } from '@taxfix/ui';
import { TaxfixSlider } from './components/slider/slider';
import React, { useState } from 'react';
import './App.css';
import { theme } from './theme';
import Logo from './images/logo.svg';
import { EquipmentOptions, ResultPanel, Controls } from './components';
import { Item } from './types';
import { computeItemDepreciationPlan } from './functions/depreciation';

type DepriciationResult = Record<string, [string, string]>;

const calculateDepriciation = (
  month: number,
  amount: number,
  selectedItem: Item
): DepriciationResult | null => {
  const date = new Date(`2021-${month + 1}-01`);
  // Default is 3 years as with the laptop
  let years = 3;

  if (selectedItem === Item.phone) {
    years = 5;
  }

  if (selectedItem === Item.desk) {
    years = 13;
  }

  if (amount) {
    const result = computeItemDepreciationPlan(date, amount, years);
    const returnValue = {} as DepriciationResult;
    for (const [key, val] of Object.entries(result)) {
      returnValue[key] = [
        parseFloat(val.value).toLocaleString('de', {
          currency: 'EUR',
          style: 'currency',
          maximumFractionDigits: 2,
        }),
        parseFloat(val.residual).toLocaleString('de', {
          currency: 'EUR',
          style: 'currency',
          maximumFractionDigits: 2,
        }),
      ];
    }
    return returnValue;
  } else {
    return null;
  }
};

function App() {
  const [selectedItem, setSelectedItem] = useState<Item>(Item.laptop);
  const [month, setMonth] = useState(0);
  const [amount, setAmount] = useState(0);

  const result = calculateDepriciation(month, amount, selectedItem);

  const onSelect = (item: Item) => {
    setMonth(0);
    setAmount(0);
    setSelectedItem(item);
  };

  return (
    <ThemeProvider theme={theme}>
      <Container maxWidth="lg" disableGutters style={{ marginBottom: '200px' }}>
        <Grid container spacing={0}>
          <Grid item xs={12} className="header">
            <Grid item xs={12} className="nav">
              <img src={Logo} />
            </Grid>
            <Grid item xs={6} className="intro">
              <h1>Deductibles Calculator</h1>
              <p>
                Find out how much you can deduct in your next declaration with
                our tool
                <br />
                built by tax experts.
              </p>
              <button onClick={() => (window.location.href = '#content')}>
                Calculate now
              </button>
            </Grid>
          </Grid>
          <Grid item xs={12} className="content" id="content">
            <EquipmentOptions
              onClick={onSelect}
              selectedOption={selectedItem}
            />
            <Controls
              selectedItem={selectedItem}
              month={month}
              amount={amount}
              onChangeMonth={setMonth}
              onChangeAmount={setAmount}
            />

            {result && <ResultPanel result={result} />}
            <Grid item xs={12} className="terms">
              <p>
                <strong>Terms and conditions:</strong> This results donâ€™t mean
                the refund you might get. It is important there is a clear link
                between your job and the equipment you bought. The tax
                authorities might ask you the proof, how much (how many %) you
                use it for work.
              </p>
            </Grid>
          </Grid>
        </Grid>
      </Container>
    </ThemeProvider>
  );
}

export default App;
